@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Zen+Dots&display=swap");

.XO-Box {
  width: 700px;
  height: 700px;
  background-color: rgba(0, 0, 0, 0.778);
  border-radius: 10px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
h1 {
  color: rgb(255, 231, 237);
  margin: 20px 0;
  font-size: 60px;
}
.zendots {
  font-family: "Zen Dots", cursive;
}
/* xo */
.XO-Game {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 600px;
  width: 600px;
  background-color: rgba(232, 232, 232, 0);
  margin: 0 0 30px 0;
}
.XO-Item{
    width: 33%;
    height: 33%;
    background: linear-gradient(to right bottom,rgba(255, 20, 51, 0.575),rgba(10, 2, 66, 0.673));
    margin: 1px;
    /* background-image: url("../../images/x.jpg"); */
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
.XO-Item.none{
  background:none;
  border: 4px solid white;
}
.butsGeneral{
  position: absolute;
  top: 0;
  left: 50px;
  display: flex;
  justify-content:center;
  align-items: flex-start;
  flex-direction: column;
}
.butsGeneral button{
  background:linear-gradient(to right bottom ,rgb(255, 0, 132),rgb(255, 0, 38)) ;
  color: white;
  font-family: "Zen Dots", cursive;
  border: none; margin-left: 2px;
  font-size: 20px;
  padding: 1px 3px;
  box-shadow: 0px 0px 10px rgb(255, 0, 34),0px 0px 2px rgba(255, 255, 255, 0.599);
}
.Music button:nth-child(2){
margin: 0 0 0 10px;
}   