.modal-cont *{
    font-family: "Zen Dots", cursive;
}
.modal-cont{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background:linear-gradient( to right bottom ,rgba(39, 15, 255, 0.355),rgba(255, 15, 15, 0.355)) ;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .4s ease-in-out;
    opacity: 1;
}
.modal-box{
    width: 60vw;
    height: 70vh;
    border-radius: 10px 30px;
    background:linear-gradient( to right bottom ,rgb(255, 3, 121),rgba(255, 56, 56, 0.866)) ;
    display: flex;align-items: center;
    justify-content: space-around;
    flex-direction: column;
} 
.modal-head-text{
    font-size: 60px;
    
    color: rgb(255, 255, 255);

}
.modal-buttons{
    display: flex;
    margin: 0 0 30px 0;
}
.modal-buttons button{
    padding: 10px 30px;
    border: none;
    color: rgba(255, 0, 38, 0.814);
    font-size: 50px;
}
